/*
 *  Judge Reviews
 *    Styles for the judge reviews section.
 *
 *  Version:
 *    1.0.0 - 2022/01/06
 */

.c-judge-reviews {
  background-color: $s-color--background-subdued;

  &.is-hidden{
    display: none;
  }

  &__inner{
    padding: 2rem $s-gutter--edge-mobile;
  }

  &__title {
    margin-bottom: 2rem;
    text-align: center;
  }

  &__widget {
    max-width: 53.75rem;
    margin: 0 auto;
  }

  .jdgm-rev-widg {
    padding: 0;
    border: 0;
    margin-top: 0;
    margin-bottom: 0;

    &__header {
      display: flex;
      justify-content: space-between;
      padding: 2rem;
      border-radius: 0.75rem;
      background-color: $s-color--background-widget-10;
    }

    &__title, &__summary-text, .jdgm-histogram, &__sort-wrapper, .jdgm-widget-actions-wrapper
    .jdgm-rich-snippet, .jdgm-widget-actions-wrapper, .jdgm-rev__br, .jdgm-form-wrapper,
    .jdgm-form-dynamic-wrapper {
      display: none;
    }

    .jdgm-star {
      color: $s-color--background-widget;
    }

    .jdgm-rev {
      padding: 2rem;
      border-top: 0;
      margin-top: 0.75rem;
      border-radius: 0.75rem;
      background-color: $s-color--background-widget-10;

      &[data-verified-buyer=true] .jdgm-rev__icon:not(.jdgm--loading):after {
        display: none;
      }

      &__location {
        display: none;
      }

      &__buyer-badge {
        @include t-font--paragraph-2;
        font-size: 0.75rem;
        padding: 0;
        background-color: transparent;
        color: $s-color--font-subdued-40;

        &-wrapper {
          grid-area: 2/2/4/3;
        }

        &:before {
          content: 'Verified Buyer';
        }
      }

      &__header {
        display: grid;
        grid-template-columns: 3rem 1fr 4.25rem;
        grid-template-rows: repeat(2, 1.5rem);
        gap: 0 0.75rem;
        margin-bottom: 1rem;

        &:after {
          display: none;
        }
      }

      &__icon {
        @include t-font--paragraph-2;
        grid-area: 1/1/2/4;
        float: none;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 3rem;
        height: 3rem;
        margin-right: 0;
        color: $s-color--font;
        background-color: $s-color--background-subdued;
      }

      &__author {
        @include t-font--paragraph-1;

        &-wrapper {
          grid-area: 1/2/2/3;
        }
      }

      &__rating {
        grid-area: 1/3/2/4;
      }

      .jdgm-star {
        font-size: 0.625rem;
      }

      &__title {
        @include t-font--paragraph-1;
        margin-bottom: 0.75rem;
      }

      &__body {
        p {
          @include t-font--paragraph-2;
        }

        &-read-more {
          @include t-anim--transition((background-size, color), $ease: linear);
          background-image: linear-gradient(rgba($s-color--font-focus, 0.0), rgba($s-color--font-focus, 0.0)), linear-gradient($s-color--font-focus, $s-color--font-focus);
          background-size: 0 0.0625rem, 100% 0.0625rem;
          background-position: 100% 100%, 0 100%;
          background-repeat: no-repeat;
          color: $s-color--font-focus;
        }
      }

      /*
        Not included in the design and couldn't see any reviews using this. Will
        hide this to make sure that it doesn't break anything in future if
        enabled later.
      */
      &__vids {
        display: none;
      }

      &__pics {
        display: flex;
        gap: 0.4625rem;
        overflow: initial;
      }

      &__pic {
        &-link {
          position: relative;
          width: 3.0469rem;
          height: 3.0469rem;
          margin-top: 1.25rem;
          margin-bottom: 0;
          opacity: 1;
          overflow: hidden;
        }

        &-img {
          height: 100%;
          width: 100%;
          border-radius: 0;
          transform: none;
          top: initial;

          @supports(object-fit: cover) {
            object-fit: cover;
            height: 100%;
          }
        }
      }
    }

    .jdgm-paginate {
      padding-top: 3.25rem;
      font-weight: 500;
      width: 100%;
      text-align: center;
      letter-spacing: 0;
      display: flex;
      justify-content: center;
      position: relative;
      align-items: center;

      &__page {
        @include t-font--root;
        display: inline-block;
        min-width: 0;
        padding: 0;
        margin: 0 1.125rem;

        &:last-child {
          margin-right: 0;
        }

        &.jdgm-curt {
          color: $s-color--font-focus;
          border-bottom: 0.125rem solid $s-color--font-focus;

          &:hover {
            color: $s-color--font-focus;
          }
        }
      }

      &__next-page,
      &__prev-page {
        font-size: 0;
        width: 1.25rem;
        height: 1.25rem;
        background: url('../../assets/icon-arrow.svg') no-repeat center;
        display: block;
        cursor: pointer;
        overflow: hidden;

        a {
          display: inline-block;
          width: 100%;
          height: 100%;
        }

        // Created using this: https://codepen.io/sosuke/pen/Pjoqqp
        &:hover {
          filter: invert(71%) sepia(12%) saturate(1577%) hue-rotate(326deg) brightness(95%) contrast(88%);
        }
      }

      &__next-page {
        margin-left: 0.5rem;
      }

      &__prev-page {
        transform: rotate(180deg);
        margin-right: 0.5rem;
      }

      &__first-page, &__last-page {
        display: none;
      }
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {

    &__inner{
      padding: 4.375rem min(10%, 12.5rem);
    }

    .jdgm-rev-widg {
      &__header {
        border-radius: 1.5rem;
      }

      .jdgm-rev {
        border-radius: 1.5rem;

        &__icon {
          border-radius: 0;
        }

        &__buyer-badge {
          font-size: $s-font--paragraph-2-size;
        }

        &__author, &__title {
          @include t-font--paragraph-1-laptop;
        }

        &__header {
          grid-template-columns: 3.75rem 1fr 8.5rem;
          grid-template-rows: repeat(2, 1.875rem);
          gap: 0 1.6875rem;
        }

        &__rating {
          text-align: right;
        }

        &__content {
          display: grid;
          grid-template-columns: 3.75rem 1fr 8.5rem;
          gap: 0 1.6875rem;
        }

        &__title {
          grid-area: 1/2/2/3;
        }

        &__body {
          grid-area: 2/2/3/3;
        }

        &__pics {
          display: grid;
          grid-area: 2/3/3/4;
          grid-template-columns: repeat(2, 4rem);
          gap: 0.5rem;
          direction: rtl;
        }

        &__pic {
          &-link {
            display: none;
            position: relative;
            width: auto;
            padding-bottom: 100%;
            margin-top: 0;
            margin-right: 0;

            &:nth-child(1), &:nth-child(2) {
              display: block;
            }
          }

          &-img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
          }
        }
      }

      .jdgm-paginate {
        &__page {
          @include t-font--heading-3;
          color: $s-color--font;
          margin: 0 1.375rem;
        }

        &__next-page,
        &__prev-page {
          width: 2rem;
          height: 2rem;
          font-size: 0;
        }

        &__next-page {
          margin-left: 0.75rem;
        }

        &__prev-page {
          margin-right: 0.75rem;
        }
      }
    }
  }

  @include t-media-query($s-xlarge-up) {

    &__inner{
      padding: 6.75rem min(10%, 12.5rem);
    }

    &__title {
      margin-bottom: 3rem;
    }

    .jdgm-rev-widg {
      &__header {
        padding: 3rem;
      }

      .jdgm-rev {
        margin-top: 1rem;
        padding: 3rem;

        &__header {
          gap: 0 2.625rem;
          margin-bottom: 0.3125rem;
        }

        &__author, &__title {
          @include t-font--paragraph-1-desktop;
        }

        .jdgm-star {
          font-size: 0.875rem;
        }

        &__content {
          gap: 0 2.625rem;
        }
      }
    }
  }
}
